/*==================================================
src/components/Home.js

The Home component displays the homepage with a bank image,
navigation links, and the account balance.
==================================================*/
import React, { Component } from 'react';
import AccountBalance from './AccountBalance';

class Home extends Component {
  render() {
    return (
      <div style={{ textAlign: 'center', padding: '30px' }}>
        <img
          src="https://i.pinimg.com/originals/ca/3e/56/ca3e56c1e4256b9e45a31320ee476e6c.gif"
          alt="Bank Icon"
          width="500"
        />

        <h1 style={{ color: '#FA77FA', marginTop: '10px', fontWeight: 'bold', fontFamily: 'Georgia, serif',
        textShadow: '1px 1px 0 #FFFFFF, -1px -1px 0 #FFFFFF, 1px -1px 0 #FFFFFF, -1px 1px 0 #836FFF' }}>Bank of React</h1>

        <AccountBalance accountBalance={this.props.accountBalance} />
      </div>
    );
  }
}

export default Home;
